//statemutationsactions
const state = {
    list: []
};

// 
const getters = {
    getList(state) { //collects
        return state.list;
    }
};

//
const mutations = {
    setList(state, list) {
        state.list = list;
    }
};

//
const actions = {
    async initList(context, list) {
        context.commit('setList', list)
    }
};

export default {
    namespaced: true,
    state,
    mutations,
    actions
}